<template>
  <div>
    <van-nav-bar
      title="TodoList"
      left-text=""
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
      class="shawdow"
    />
    <!--<form action="/">-->
    <!--<van-search-->
    <!--v-model="value"-->
    <!--placeholder="请输入搜索关键词"-->
    <!--@search="onSearch"-->
    <!--/>-->
    <!--</form>-->
    <div class="logoWrapper">
      <img class="logo" src="../assets/2.gif" />
    </div>
    <div class="header1">
      事项提醒列表:
    </div>
    <div class="wrapper">
      <div class="title1 title">Ⅰ&nbsp;&nbsp;&nbsp;重要且紧急</div>

      <div class="divder" v-for="(item, index) in value1">
        <van-swipe-cell>
          <template slot="left">
            <van-button
              square
              type="primary"
              :text="
                item.pushTime ? 'once:' + item.pushTime : 'more:' + item.cron
              "
            />
          </template>
          <van-cell :border="false" :title="item.content" />

          <template slot="right">
            <van-button
              square
              type="danger"
              @click="
                del(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="删除"
            />
            <van-button
              square
              type="primary"
              @click="
                done(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="完成"
            />
          </template>
        </van-swipe-cell>
      </div>
    </div>

    <div class="wrapper">
      <div class="title2 title">Ⅱ&nbsp;&nbsp;&nbsp;重要不紧急</div>
      <div class="divder" v-for="(item, index) in value2">
        <van-swipe-cell>
          <template slot="left">
            <van-button
              square
              type="primary"
              :text="
                item.pushTime ? 'once:' + item.pushTime : 'more:' + item.cron
              "
            />
          </template>
          <van-cell :border="false" :title="item.content" value="" />

          <template slot="right">
            <van-button
              square
              type="danger"
              @click="
                del(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="删除"
            />
            <van-button
              square
              type="primary"
              @click="
                done(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="完成"
            />
          </template>
        </van-swipe-cell>
      </div>
    </div>
    <div class="wrapper">
      <div class="title3 title">Ⅲ&nbsp;&nbsp;&nbsp;紧急不重要</div>
      <div class="divder" v-for="(item, index) in value3">
        <van-swipe-cell>
          <template slot="left">
            <van-button
              square
              type="primary"
              :text="
                item.pushTime ? 'once:' + item.pushTime : 'more:' + item.cron
              "
            />
          </template>
          <van-cell :border="false" :title="item.content" value="" />

          <template slot="right">
            <van-button
              square
              type="danger"
              @click="
                del(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="删除"
            />
            <van-button
              square
              type="primary"
              @click="
                done(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="完成"
            />
          </template>
        </van-swipe-cell>
      </div>
    </div>
    <div class="wrapper">
      <div class="title4 title">Ⅳ&nbsp;&nbsp;&nbsp;不紧急不重要</div>
      <div class="divder" v-for="(item, index) in value4">
        <van-swipe-cell>
          <template slot="left">
            <van-button
              square
              type="primary"
              :text="
                item.pushTime ? 'once:' + item.pushTime : 'more:' + item.cron
              "
            />
          </template>
          <van-cell :border="false" :title="item.content" value="" />

          <template slot="right">
            <van-button
              square
              type="danger"
              @click="
                del(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="删除"
            />
            <van-button
              square
              type="primary"
              @click="
                done(
                  index,
                  item.todo.id,
                  item.jobKey,
                  item.triggerKey,
                  item.pushTime
                )
              "
              text="完成"
            />
          </template>
        </van-swipe-cell>
      </div>
    </div>

    <van-divider>end</van-divider>
  </div>
</template>

<script>
import axios from "@/axios";
import mixin from "@/mixins/mixin.js";
import formatter from "@/utils/DateTimeUtil";
export default {
  mixins: [mixin],
  name: "TodoList",
  data() {
    return {
      value1: [],
      value2: [],
      value3: [],
      value4: []
    };
  },
  created() {
    this.updateDate();
  },
  methods: {
    updateDate() {
      axios.get("/api/get_todolist").then(res => {
        if (res.data.code === 200) {
          let data = res.data.data;
          this.value1 = [];
          this.value2 = [];
          this.value3 = [];
          this.value4 = [];
          data.forEach(item => {
            console.log(item.pushTime);
            // if (item.push_time != null) {
            //   let date = new Date(item.push_time);
            //   let pushTime = formatter(date);
            //   item.pushTime = pushTime;
            // }
            if (item.todo.type === 1) {
              this.value1.push(item);
            }
            if (item.todo.type === 2) {
              this.value2.push(item);
            }
            if (item.todo.type === 3) {
              this.value3.push(item);
            }
            if (item.todo.type === 4) {
              this.value4.push(item);
            }
          });
        }
      });
    },
    onClickRight() {
      Toast("按钮");
    },
    onSearch() {},
    onCancel() {},
    del(index, id, jobKey, triggerKey, pushTime) {
      axios({
        method: "delete",
        url: "/api/delete_todo",
        data: {
          id: id,
          jobKey: jobKey,
          triggerKey: triggerKey,
          pushTime: pushTime
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.updateDate();
          this.$toast("删除成功");
        }
      });
    },
    done(index, id, jobKey, triggerKey, pushTime) {
      axios({
        method: "put",
        url: "/api/done_todo",
        data: {
          id: id,
          jobKey: jobKey,
          triggerKey: triggerKey,
          pushTime: pushTime
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.updateDate();
          this.$toast("更新成功");
        }
      });
    }
  }
};
</script>

<style scoped lang="stylus">
.wrapper
    margin 18px 0
.title
    text-indent 10px
    height 38px
    line-height @height
.title1
    color #fb8d83
    background #ffeee7
.title2
    color #f7812a
    background #ffefcb
.title3
    color #41b427
    background #effedf
.title4
    color #24afdd
    background #d6f4fe
.divder
  border-bottom: 1px solid #ebedf0;

/*.wrapper*/
    /*border-bottom 1px solid;*/
</style>
